* {
  margin: 0;
  padding: 0;
  font-family: 'Bebas Neue', Impact, 'Arial Narrow Bold', sans-serif;
}

body {
  background-color: black; /* Set the background color of the body to black */
  color: white; /* Optional: set the text color to white to contrast with the black background */
}

.main {
  width: 100%;
  height: 100vh;
}

video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.takapic {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;

  font-size: 4rem;
  color: white;  /* White text color */
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.5), 0 0 20px rgba(255, 255, 255, 0.5), 0 0 30px rgba(255, 255, 255, 0.5), 0 0 40px rgba(255, 255, 255, 0.6);
}

/* Media query for mobile devices */
@media (max-width: 768px) {
  .takapic {
    font-size: 1.8rem;  /* Adjust the font size for smaller screens */
  }
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0);
}




/* Call to Action Section ///////////////////////////////////////////////////////////////////////////// */
.cta-section {
  background-color: #000;
  color: white;
  padding: 40px 0;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

/* Text styling */
.cta-section p {
  font-size: 2.5rem;  /* Increased from previous size */
  margin-top: 40px;
  margin-bottom: 10px;
  text-align: center;
}

/* Button styling */
.cta-button {
  padding: 15px 30px;
  font-size: 1.2rem;
  background-color: #ff4500;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

.cta-button:hover {
  background-color: #e03e00;
}



/* TOP LEFT LOGO //////////////////////////////////////////////////////////////////////////////////// */
.spinning-logo {
  position: fixed;
  top: 12px;
  left: 12px;
  width: 50px; /* Adjust size as needed */
  height: auto;
  z-index: 1000; /* Ensures logo stays on top of other content */
  animation: spin 10s linear infinite; /* Adjust duration as needed */
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.brand-text {  position: fixed;
  top: 15px;
  left: 65px;
  font-size: 2.5rem;/* Adjust size as needed */
  height: auto;
  z-index: 1000; /* Ensures logo stays on top of other content */
  background: linear-gradient(
    to right,
    #4F82AF,
    #4AA1A3,
    #5979E0,
    #C95E89,
    #E58356,
    #4F82AF
  );
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  background-size: 200% auto;
  animation: gradient 10s linear infinite;
}

@keyframes gradient {
  to {
    background-position: 200% center;
  }
}

.upload-button {
  position: fixed;
  top: 15px;
  right: 15px;
  padding: 12px 16px;
  font-size: 1.2rem;

  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: bold;
  
  border: none;
  border-radius: 5px;
  cursor: pointer;
  z-index: 1000;
  background: linear-gradient(
    to right,
    #4F82AF,
    #4AA1A3,
    #5979E0,
    #C95E89,
    #E58356,
    #4F82AF
  );
  background-size: 200% auto;
  color: white;
  animation: gradient 10s linear infinite;
  transition: transform 0.2s;
}

.upload-button:hover {
  transform: scale(1.05);
}

.upload-button-main {
  padding: 12px 16px;
  font-size: 1.2rem;

  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: bold;
  
  border: none;
  border-radius: 5px;
  cursor: pointer;
  z-index: 1000;
  background: linear-gradient(
    to right,
    #4F82AF,
    #4AA1A3,
    #5979E0,
    #C95E89,
    #E58356,
    #4F82AF
  );
  background-size: 200% auto;
  color: white;
  animation: gradient 10s linear infinite;
  transition: transform 0.2s;
  display: block;
  margin: 0 auto;
}

.upload-button-main:hover {
  transform: scale(1.05);
}


.template-image {
  width: 100%;
  max-width: 800px;  /* Adjust this value as needed */
  margin: 20px auto;
  display: block;
}


.bottom-spacing {
  height: 100px;  /* Adjust this value to increase/decrease space */
  width: 100%;
}





.genres {
  display: flex; /* Arrange icons in a row */
  justify-content: center; /* Center icons horizontally */
  align-items: center; /* Align icons vertically */
  gap: 10px; /* Space between icons */
  position: relative; /* Enable positioning within this container */
  top: -50px; /* Move the icons up over the wave */
  z-index: 1; /* Ensure icons appear above the wave SVG */
}

.genre-icon {
  width: 120px; /* Adjust size for better visibility */
  height: auto; /* Maintain aspect ratio */
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25)); /* Add subtle shadow */
  transition: transform 0.3s ease; /* Smooth scaling on hover */
}

.genre-icon:hover {
  transform: scale(1.2); /* Enlarge slightly on hover for interactivity */
}

/* Media Query for Tablets (768px and below) */
@media (max-width: 768px) {
  .genre-icon {
    width: 70px; /* Smaller size for tablets */
  }
  .genres {
    gap: 8px; /* Space between icons */
  }
}


/* Media Query for Mobile Phones (480px and below) */
@media (max-width: 480px) {
  .genre-icon {
    width: 50px; /* Even smaller size for mobile */
  }
  .genres {
    gap: 3px; /* Space between icons */
  }
}






.video-container {
  position: relative;
  width: 100%;
  height: 100vh; /* Full viewport height */
  overflow: hidden;
}

video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.wave-divider {
  position: absolute;
  bottom: 0px;
  width: 100%;
  height: 200px; /* Adjust based on wave height */
  overflow: hidden;
  z-index: 1;  /* Make sure it's below the emojis */
  
}

.wave-divider svg {
  display: block;
  width: 100%; /* Ensures full width */
  height: 100%; /* Scales proportionally */

}
